import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';

@Injectable({
    providedIn: 'root',
})
export class ScreenDetectorService {
    private $isMobile: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

    constructor(private breakpointObserver: BreakpointObserver) {
        this.addScreenSizeListeners();
    }

    get isMobile(): BehaviorSubject<boolean> {
        return this.$isMobile;
    }

    private addScreenSizeListeners(): void {
        this.breakpointObserver
            .observe(['(max-width: 1220px)', '(min-width: 1221px)'])
            .subscribe((state: BreakpointState) => {
                if (state.breakpoints['(max-width: 1220px)']) {
                    if (!this.$isMobile.getValue()) {
                        this.$isMobile.next(true);
                    }
                }
                if (state.breakpoints['(min-width: 1221px)']) {
                    if (this.$isMobile.getValue()) {
                        this.$isMobile.next(false);
                    }
                }
            });
    }
}
