import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { BookingSummary } from '@schemas/modules/bookings/booking-summary';
import { ICart } from '@schemas/modules/cart/cart';
import { IFlightsCartData } from '@schemas/modules/flights/flights-cart-data';

@Component({
    selector: 'tz-mob-booking-summary',
    templateUrl: './mob-booking-summary.component.html',
    styleUrls: ['./mob-booking-summary.component.scss'],
})
export class MobBookingSummaryComponent {
    @Input() bookingSummary!: BookingSummary;
    @Input() transferCartData: ICart | undefined;
    @Input() flightsCartData: IFlightsCartData | undefined;
    @Input() accommodationCartData: IAccommodationsCartData | undefined;
    @Input() carRentalCartData: ICart | undefined;
    @Input() trainCartData: ICart | undefined;
    @Input() incompleteOrCancelBookings = false;

    @Output() cancel = new EventEmitter<string[]>();

    public selectedTab: 0 | 1 = 0;

    constructor() {}

    public onCancel(bookingIds: string[]): void {
        this.cancel.emit(bookingIds);
    }
}
