import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CartStatus } from '@core/constants/cart-status';
import { ServiceId } from '@core/constants/service-id.const';
import { ToolsService } from '@core/utils/tools-service';
import { TranslateService } from '@ngx-translate/core';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { BookingCart, BookingSummary } from '@schemas/modules/bookings/booking-summary';
import { ICart } from '@schemas/modules/cart/cart';
import { IFlightsCartData } from '@schemas/modules/flights/flights-cart-data';
import { DialogService } from '@services/dialog.service';
import { BookingDataService } from '@services/modules/bookings/dataservices/booking.data.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { UserService } from '@services/modules/user/services/user.service';
import { SettingsService } from '@services/services/settings-service';
import { skip, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-booking',
    templateUrl: './booking.component.html',
    styleUrls: ['./booking.component.scss'],
})
export class BookingComponent implements OnInit, OnDestroy {
    public bookingSummary!: BookingSummary;
    public transferCartData: ICart | undefined;
    public flightsCartData: IFlightsCartData | undefined;
    public accommodationCartData: IAccommodationsCartData | undefined;
    public carRentalCartData: ICart | undefined;
    public trainCartData: ICart | undefined;
    public incompleteOrCancelBookings = false;

    private bookingId: string;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private activeRoute: ActivatedRoute,
        private userService: UserService,
        private bookingDataService: BookingDataService,
        private cartService: CartService,
        private dialogService: DialogService,
        private settingsService: SettingsService,
        private translateService: TranslateService,
        private tzCurrencyPipe: TzCurrencyPipe,
        public platformService: PlatformService
    ) {
        this.bookingId = this.activeRoute.snapshot.params['id'];
    }

    ngOnInit() {
        this.initializeBookingSummaryComponent();
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onDownloadBookingVoucher(): void {
        this.bookingDataService.downloadBookingVoucher(this.bookingId).subscribe((res) => {
            const newBlob = new Blob([res], { type: 'application/pdf' });
            const windowUrl = window.URL || window.webkitURL;
            const url = windowUrl.createObjectURL(newBlob);

            const link = document.createElement('a');
            link.href = url;
            link.download = `Trazler_Booking_${this.bookingSummary.booking.bookingNumber}.pdf`;
            link.dispatchEvent(new MouseEvent('click', { bubbles: true, cancelable: true, view: window }));

            windowUrl.revokeObjectURL(url);
            this.dialogService.openSnackBar({
                message: this.translateService.instant('NOTIFS.VOUCHER_LAUNCHED'),
            });
        });
    }

    public onResendBookingMail(): void {
        this.bookingDataService.sendBookingMail(this.bookingId).subscribe(
            () => {
                this.dialogService.openSnackBar({
                    message: this.translateService.instant('NOTIFS.BOOKING_MAIL_SEND'),
                });
            },
            (failure: any) => {
                this.dialogService.openSnackBar({ message: failure });
            }
        );
    }

    public onCancel(optionsIds: string[]): void {
        const cancelOptions = this.fetchCancelOptions(optionsIds);
        if (cancelOptions.some((option) => option.cancelMode === 'denied')) {
            this.dialogService.openConfirmationDialog({
                content: this.translateService.instant('POPUPS.SUPPORT_MSG.BODY'),
                confirmBtnTitle: this.translateService.instant('POPUPS.SUPPORT_MSG.OK'),
            });
            return;
        }

        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.CONFIRM'),
                multipleContent: [this.formCancellationMessage(cancelOptions)],
                confirmBtnTitle: this.translateService.instant('POPUPS.YES'),
                cancelBtnTitle: this.translateService.instant('POPUPS.NO'),
            })
            .afterClosed()
            .subscribe((cancelBooking: boolean) => {
                if (!cancelBooking) {
                    return;
                }

                for (const option of cancelOptions) {
                    option.status = CartStatus.STATUS_CPROGRESS;
                }
                this.formBooking();

                this.bookingDataService.cancelBooking(this.bookingId, optionsIds).subscribe(
                    (res) => {
                        this.fetchBookingSummary();
                        this.dialogService.openSnackBar({ message: res.message });
                    },
                    (failure: any) => {
                        this.fetchBookingSummary();
                        //this.dialogService.openSnackBar({ message: failure.error.message });
                    }
                );
            });
    }

    private fetchCancelOptions(optionsIds: string[]): ICartSelectedItemBookingOption[] {
        const bookingOptions: ICartSelectedItemBookingOption[] = [];
        for (const item of this.bookingSummary.cart.items) {
            for (const option of item.selectedItem.bookingOptions) {
                if (optionsIds.includes(option.itineraryId)) {
                    bookingOptions.push(option);
                }
            }
        }

        return bookingOptions;
    }

    private formCancellationMessage(options: ICartSelectedItemBookingOption[]): string {
        let message = '';
        for (const option of options) {
            console.log(option);
            const cp = option.applicableCp;
            if (cp.info !== '') {
                message += `<div style="font-weight:bold;">${option.applicableCp.info} </div>`;
            }

            let translationKey = 'BOOKINGS.CANCELLATION_ALERT.';
            if (cp.policyType === 'Non-refundable') {
                translationKey += 'CONFIRM_MSG_NR';
            } else if (cp.isFree) {
                translationKey += 'CONFIRM_MSG_FREE';
            } else {
                translationKey += 'CONFIRM_MSG_CHARGE';
            }

            const amt = this.tzCurrencyPipe.transform(cp.penaltyAmt, cp.curCode);
            message += this.translateService.instant(translationKey, { amt }) + '<br/><br/>';
        }

        if (message === '') {
            message = this.translateService.instant('BOOKINGS.CANCELLATION_ALERT.CONFIRM_MSG');
        }

        message += this.translateService.instant('BOOKINGS.CANCELLATION_ALERT.CONTINUE_MSG') + '<br/>';
        return message;
    }

    private formBooking(): void {
        const cartData = ToolsService.deepCopy(this.bookingSummary.cart) as BookingCart;
        this.flightsCartData = this.cartService.getCartData(ServiceId.FLIGHT, cartData);
        this.accommodationCartData = this.cartService.getCartData(ServiceId.ACC, cartData);
        this.transferCartData = this.cartService.getCartData(ServiceId.TRANSFER, cartData);
        this.carRentalCartData = this.cartService.getCartData(ServiceId.CAR_RENTAL, cartData);
        this.trainCartData = this.cartService.getCartData(ServiceId.TRAIN, cartData);

        for (const item of cartData.items) {
            for (const option of item.selectedItem.bookingOptions) {
                if (option.status < CartStatus.STATUS_CONFIRMED) {
                    this.incompleteOrCancelBookings = true;
                }
            }
        }
        //handle case where all items are cancelled
        if (!this.incompleteOrCancelBookings && !CartUtils.hasStillBookedItem(cartData)) {
            this.incompleteOrCancelBookings = true;
        }
    }

    private fetchBookingSummary(): void {
        this.bookingDataService.getBooking(this.userService.getCustomerInformation().id, this.bookingId).subscribe(
            (booking) => {
                this.bookingSummary = booking;
                this.formBooking();
                console.log(booking);
            },
            (failure: any) => {
                console.log('Get booking failure: ', failure);
            }
        );
    }

    private initializeBookingSummaryComponent(): void {
        this.fetchBookingSummary();

        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.fetchBookingSummary();
        });
    }
}
