import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ServiceId } from '@core/constants/service-id.const';
import { TranslateService } from '@ngx-translate/core';
import { BookingSummary } from '@schemas/modules/bookings/booking-summary';
import { ICart } from '@schemas/modules/cart/cart';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { DateRangePipe } from '@shared/pipes/date-range.pipe';
import { SettingsService } from '@services/services/settings-service';
import { Subject } from 'rxjs';
import { skip, takeUntil } from 'rxjs/operators';

@Component({
    selector: 'tz-booking-summary-header',
    templateUrl: './booking-summary-header.component.html',
    styleUrls: ['./booking-summary-header.component.scss'],
})
export class BookingSummaryHeaderComponent implements OnInit, OnDestroy {
    @Input() bookingSummary!: BookingSummary;

    public personsStr = '';
    public tripDate = '';
    public transfers: {
        date: Date;
        title: string | undefined;
    }[] = [];
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private translateService: TranslateService,
        private dateRange: DateRangePipe,
        private settingsService: SettingsService
    ) {}

    ngOnInit() {
        this.formBookingInfos();
        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.tripDate = this.dateRange.transform(
                this.bookingSummary.booking.startDate,
                this.bookingSummary.booking.endDate,
                true
            );
        });
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    private formBookingInfos(): void {
        const travellersData = CartUtils.getTravellersData(this.bookingSummary.cart.items);
        if (travellersData) {
            this.personsStr =
                travellersData.txt +
                ' ' +
                this.translateService
                    .instant('TRAVELLER.' + (travellersData.total > 1 ? 'PERSONS' : 'PERSON'))
                    .toLowerCase();
        }

        const hasMultipleServices = this.bookingSummary.cart.items.some(
            (item) => item.serviceId !== ServiceId.TRANSFER
        );
        if (hasMultipleServices) {
            const booking = this.bookingSummary.booking;
            this.tripDate = this.dateRange.transform(booking.startDate, booking.endDate, true);
        } else {
            const cartDataWithTitle = CartUtils.formServiceTitle(
                this.bookingSummary.cart,
                this.translateService
            ) as ICart;
            cartDataWithTitle.items.forEach((item) => {
                const info = {
                    date: new Date(item.selectedItem.data.onwardDetails.pickupTzDate),
                    title: item.summaryHeaderInfo,
                };

                this.transfers.push(info);
            });
        }
    }
}
