import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { ToolsService } from '@core/utils/tools-service';
import { TranslateService } from '@ngx-translate/core';
import { BookingCart, BookingSummary } from '@schemas/modules/bookings/booking-summary';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import { SelectedBagOption, SelectedLuggageAmount } from '@schemas/modules/checkout/transport-luggage';
import { SelectedInsurance } from '@schemas/modules/checkout/travel-insurance';
import { TzCheckoutService } from '@services/modules/checkout/services/tz-checkout.service';

@Component({
    selector: 'tz-booking-summary-info',
    templateUrl: './booking-summary-info.component.html',
    styleUrls: ['./booking-summary-info.component.scss'],
})
export class BookingSummaryInfoComponent implements OnChanges, OnInit {
    @Input() bookingSummary!: BookingSummary;

    public cartSummary: CheckoutCartServiceSummary[] = [];
    public cartData!: BookingCart;
    public selectedInsurance = { selectedIdx: -1 } as SelectedInsurance;
    public additionalLuggagePrice = { listPrice: 0, listPriceCur: '' } as SelectedLuggageAmount;
    public additionalLuggages: { type: string; luggageText: string }[] = [];

    public travellers: CheckoutTraveller[] = [];
    public accTravellers: CheckoutTraveller[] = [];
    public travellerNames: string[] = [];
    public transferTraveller!: CheckoutTraveller;
    public driverInfo!: CheckoutTraveller;

    constructor(private tzCheckoutService: TzCheckoutService, private translateService: TranslateService) {}

    private static formLuggageText(option: SelectedBagOption): string {
        let txt = 'pc x ';
        if (option.count > 1) {
            txt = 'pcs x ';
        }

        if (option.weight === 0) {
            return option.count + ' ' + txt;
        }

        // 2 pcs x 23kg
        return option.count + ' ' + txt + option.weight + 'kg';
    }

    ngOnInit() {
        this.init();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (
            changes['bookingSummary'] &&
            changes['bookingSummary'].currentValue &&
            !changes['bookingSummary'].firstChange
        ) {
            this.init();
        }
    }

    private formSummaryLuggageInfo(): void {
        const booking = this.bookingSummary.booking;

        this.additionalLuggagePrice = {
            listPrice: booking.additionalLuggagePrice.listPrice,
            listPriceCur: booking.additionalLuggagePrice.listPriceCur,
        };

        const tmpLuggageMap: { [key: string]: SelectedBagOption } = {};
        let byTotalWeight: boolean;

        for (const traveller of booking.travellers) {
            if (!traveller.additionalLuggage || !traveller.additionalLuggage.checkIn.options) {
                continue;
            }

            byTotalWeight = traveller.additionalLuggage.checkIn.byTotalWeight;
            for (const i in traveller.additionalLuggage.checkIn.options) {
                if (!traveller.additionalLuggage.checkIn.options.hasOwnProperty(i)) {
                    continue;
                }

                const option = traveller.additionalLuggage.checkIn.options[i];
                if (tmpLuggageMap[option.weight]) {
                    if (byTotalWeight) {
                        tmpLuggageMap[option.weight].weight += option.weight * option.count;
                    }

                    tmpLuggageMap[option.weight].count += option.count;
                } else {
                    tmpLuggageMap[option.weight] = option;
                }
            }
        }

        const bagTitle = this.translateService.instant('FLIGHT.LUGGAGE.checkIn');
        for (const title in tmpLuggageMap) {
            if (!tmpLuggageMap.hasOwnProperty(title)) {
                continue;
            }

            this.additionalLuggages.push({
                type: bagTitle,
                luggageText: BookingSummaryInfoComponent.formLuggageText(tmpLuggageMap[title]),
            });
        }
    }

    private uniqueTravellers(a: CheckoutTraveller[]): CheckoutTraveller[] {
        const seen: {
            [item: string]: boolean;
        } = {};

        return a.filter((item: CheckoutTraveller): boolean => {
            if (seen[item.firstName + item.lastName]) {
                return false;
            }
            seen[item.firstName + item.lastName] = true;
            return true;
        });
    }

    private formSummaryInfos(): void {
        this.travellers = this.bookingSummary.booking.travellers;
        this.accTravellers = this.uniqueTravellers(this.bookingSummary.booking.accTravellers);
        this.transferTraveller = this.bookingSummary.booking.transferTraveller;
        this.driverInfo = this.bookingSummary.booking.driverInfo;

        this.cartSummary = this.tzCheckoutService.formCheckoutCartSummary(this.cartData.items);
        if (this.cartData.insurancePrice && this.cartData.insurancePrice.listPrice) {
            const selected = this.cartData.insuranceDataSummary.insuranceBooking;

            for (const [i, insurance] of this.cartData.insuranceDataSummary.tzInsurance.insurances.entries()) {
                if (selected.formuleRef !== insurance.formuleRef) {
                    continue;
                }

                this.selectedInsurance = {
                    selected: insurance,
                    selectedIdx: i,
                    validatedTC: true,
                };
            }
        }

        if (this.bookingSummary.booking.baggageSelected) {
            this.formSummaryLuggageInfo();
        }

        this.cartData.helpers = this.tzCheckoutService.getHelpers(
            this.cartData,
            this.additionalLuggagePrice,
            this.selectedInsurance.selected
        );
    }

    private init() {
        this.cartData = ToolsService.deepCopy(this.bookingSummary.cart) as BookingCart;
        this.formSummaryInfos();
    }
}
