import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { BookingSummary } from '@schemas/modules/bookings/booking-summary';
import { ICart } from '@schemas/modules/cart/cart';
import { IFlightsCartData } from '@schemas/modules/flights/flights-cart-data';
import { DialogService } from '@services/dialog.service';
import { ScreenDetectorService } from '@services/screen-detector.service';
import { BehaviorSubject } from 'rxjs';
import { TripTimelineDialogComponent } from 'src/app/modules/trip-timeline/components/trip-timeline-dialog/trip-timeline-dialog.component';

@Component({
    selector: 'tz-booking-summary',
    templateUrl: './booking-summary.component.html',
    styleUrls: ['./booking-summary.component.scss'],
})
export class BookingSummaryComponent {
    @Input() bookingSummary!: BookingSummary;
    @Input() transferCartData: ICart | undefined;
    @Input() flightsCartData: IFlightsCartData | undefined;
    @Input() accommodationCartData: IAccommodationsCartData | undefined;
    @Input() carRentalCartData: ICart | undefined;
    @Input() trainCartData: ICart | undefined;
    @Input() incompleteOrCancelBookings = false;

    @Output() cancel = new EventEmitter<string[]>();
    @Output() downloadVoucher = new EventEmitter<void>();
    @Output() resendEmail = new EventEmitter<void>();

    public showTimeLine = false;
    public mouseHoverId = '';
    public isMobile$?: BehaviorSubject<boolean>;

    constructor(private dialogService: DialogService, private screenSizeDetectorService: ScreenDetectorService) {
        this.isMobile$ = this.screenSizeDetectorService.isMobile;
    }

    public setHoverId(itemId: string): void {
        this.mouseHoverId = itemId;
    }

    public openBasketTimeline(): void {
        if (this.bookingSummary.cart.items.length === 0) {
            return;
        }

        this.dialogService.openBasketTimelineDialog(TripTimelineDialogComponent, this.bookingSummary.cart.items);
    }

    public onCancel(bookingIds: string[]): void {
        this.cancel.emit(bookingIds);
    }
}
